@keyframes fadein {
    0% {
        opacity:0;
    }

    100% {
        opacity:1;
    }
}

@-webkit-keyframes fadein {
    0% {
        opacity:0;
    }

    100% {
        opacity:1;
    }
}

@keyframes fadein {
    0% {
        opacity:0;
    }

    100% {
        opacity:1;
    }
}

@-webkit-keyframes fadeindelay {
    0% {
        max-height: 0;
        opacity:0;
    }

    50% {
        max-height: 0;
        opacity:0;
    }

    51% {
        max-height: 10000px;
        opacity: 0;
    }

    100% {
        max-height: 10000px;
        opacity:1;
    }
}

@keyframes fadeindelay {
    0% {
        max-height: 0;
        opacity:0;
    }

    50% {
        max-height: 0;
        opacity:0;
    }

    51% {
        max-height: 10000px;
        opacity: 0;
    }

    100% {
        max-height: 10000px;
        opacity:1;
    }
}


@-webkit-keyframes fadeoutzoom {

    0% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        opacity:1;
    }

    100% {
        -webkit-transform: scale3d(10, 10, 10);
        transform: scale3d(10, 10, 10);
        opacity:0;
    }
}

@keyframes fadeoutzoom {
    0% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
        opacity:1;
    }


    100% {
        -webkit-transform: scale3d(10, 10, 10);
        transform: scale3d(10, 10, 10);
        opacity:0;
    }
}

@-webkit-keyframes bounceIn {
    0%, 20%, 40%, 60%, 80%, 100% {
        -webkit-transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    }

    0% {
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }

    20% {
        -webkit-transform: scale3d(1.3, 1.3, 1.3);
        transform: scale3d(1.3, 1.3, 1.3);
    }

    40% {
        -webkit-transform: scale3d(.8, .8, .8);
        transform: scale3d(.8, .8, .8);
    }

    60% {
        -webkit-transform: scale3d(1.06, 1.06, 1.06);
        transform: scale3d(1.06, 1.06, 1.06);
    }

    80% {
        -webkit-transform: scale3d(.97, .97, .97);
        transform: scale3d(.97, .97, .97);
    }

    100% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}

@keyframes bounceIn {
    0%, 20%, 40%, 60%, 80%, 100% {
        -webkit-transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
        transition-timing-function: cubic-bezier(0.215, 0.610, 0.355, 1.000);
    }

    0% {
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }

    20% {
        -webkit-transform: scale3d(1.3, 1.3, 1.4);
        transform: scale3d(1.3, 1.3, 1.4);
    }

    40% {
        -webkit-transform: scale3d(.8, .8, .8);
        transform: scale3d(.8, .8, .8);
    }

    60% {
        -webkit-transform: scale3d(1.06, 1.06, 1.06);
        transform: scale3d(1.06, 1.06, 1.06);
    }

    80% {
        -webkit-transform: scale3d(.97, .97, .97);
        transform: scale3d(.97, .97, .97);
    }

    100% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}

@-webkit-keyframes bounceInOnce {
    0%, 70%, 100% {
        -webkit-transition-timing-function: cubic-bezier(0.850, 0.000, 0.250, 0.995);
        transition-timing-function: cubic-bezier(0.850, 0.000, 0.250, 0.995);
    }

    0% {
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }

    70% {
        -webkit-transform: scale3d(1.1, 1.1, .1.1);
        transform: scale3d(1.1, 1.1, .1.1);
    }

    100% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}

@keyframes bounceInOnce {
    0%, 70%, 100% {
        -webkit-transition-timing-function: cubic-bezier(0.850, 0.000, 0.250, 0.995);
        transition-timing-function: cubic-bezier(0.850, 0.000, 0.250, 0.995);
    }

    0% {
        -webkit-transform: scale3d(.3, .3, .3);
        transform: scale3d(.3, .3, .3);
    }

    70% {
        -webkit-transform: scale3d(1.1, 1.1, 1.1);
        transform: scale3d(1.1, 1.1, 1.1);
    }

    100% {
        -webkit-transform: scale3d(1, 1, 1);
        transform: scale3d(1, 1, 1);
    }
}

@-moz-keyframes spin {
    from {
        -moz-transform: rotate(0deg);
    }

    to {
        -moz-transform: rotate(360deg);
    }
}

@-webkit-keyframes spin {
    from {
        -webkit-transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes spin {
    from {
        transform: rotate(0deg);
    }

    to {
        transform: rotate(360deg);
    }
}

@-moz-keyframes spin {
    from {
        -moz-transform: rotate(0deg);
    }

    to {
        -moz-transform: rotate(360deg);
    }
}

