// Contextual backgrounds

.bg-variant(@color, @bg-text-color) {
  background-color: fadeout(@color, 60%);
  color: @bg-text-color;
  background-image: @input-bg-disabled-image;


  a {
      color:#FFF;

      &:hover, &:focus {
        text-decoration:underline !important;
      }
  }
}
